.color {

  &-white {
    color: $white;

    .btn,
    input[type=submit] {
      background: $white !important;
      color: $primary !important;
      border-color: $white !important;

      &:hover {
        opacity: .8;
      }
    }
  }

  &-primary {
    color: $primary;
  }
}